import {
	FormItem,
	FormLayout,
	Group,
	PanelHeader,
	Input,
	Button,
	ScreenSpinner,
	Text,
	Link,
} from "@vkontakte/vkui";
import React, { FormEvent, useContext } from "react";
import { login as LoginHook } from "../../hooks/auth";
import { AppContext } from "../../providers/AppProvider";

export type ErrorType = {
	type: "required" | "custom";
	message: string;
} | null;

function Login({ setActiveView }: { setActiveView: any }) {
	const [email, setEmail] = React.useState<string>("");
	const [password, setPassword] = React.useState<string>("");
	const [error, setError] = React.useState<{
		email: ErrorType;
		password: ErrorType;
	}>({ email: null, password: null });

	const handleEmailChange = (e: any) => {
		setEmail(e.target.value);
		let newError = error;
		newError["email"] = null;
		setError(newError);
		if (
			// eslint-disable-next-line
			!/^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i.test(
				e.target.value
			)
		) {
			let newError = error;

			newError["email"] = {
				type: "custom",
				message: "Email  ",
			};
			setError(newError);
		}

		if (e.target.value.trim() === "") {
			let newError = error;

			newError["email"] = {
				type: "required",
				message: "Email  ",
			};
			setError(newError);
		}
	};

	const handlePasswordChange = (e: any) => {
		setPassword(e.target.value);

		let newError = error;
		newError["password"] = null;
		setError(newError);

		if (e.target.value.trim() === "") {
			let newError = error;
			newError["password"] = {
				type: "required",
				message: "Email  ",
			};
			setError(newError);
		}
	};

	const { setProps } = useContext(AppContext);

	const onSubmit = async (e: FormEvent<HTMLFormElement>) => {
		e.preventDefault();
		setProps({
			popout: <ScreenSpinner state="loading" />,
			modal: null,
			filmId: null,
		});
		try {
			let formError: boolean = false;
			if (
				// eslint-disable-next-line
				!/^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i.test(
					email
				)
			) {
				let newError = error;

				newError["email"] = {
					type: "custom",
					message: "Email  ",
				};
				setError(newError);

				formError = true;
			}

			if (email.trim() === "") {
				let newError = error;

				newError["email"] = {
					type: "required",
					message: "Email  ",
				};
				setError(newError);
				formError = true;
			}
			if (password.trim() === "") {
				let newError = error;
				newError["password"] = {
					type: "required",
					message: "Email  ",
				};
				setError(newError);
				formError = true;
			}

			if (formError) return;

			const res = await LoginHook(email, password);

			if (res.errors) {
				setError({
					email: {
						type: "custom",
						message: "   ",
					},
					password: {
						type: "custom",
						message: "   ",
					},
				});
			} else {
				window.location.href = "/";
			}
		} finally {
			setProps({
				popout: null,
				modal: null,
				filmId: null,
			});
		}
	};

	return (
		<>
			<PanelHeader></PanelHeader>

			<Group>
				<FormLayout onSubmit={onSubmit}>
					<FormItem
						top="Email"
						status={error.email ? "error" : "default"}
						bottom={
							error.email
								? error.email.type === "required"
									? " Email"
									: error.email.message
								: null
						}
					>
						<Input value={email} onChange={handleEmailChange} />
					</FormItem>
					<FormItem
						top=""
						status={error.password ? "error" : "default"}
						bottom={
							error.password
								? error.password.type === "required"
									? " "
									: error.password.message
								: null
						}
					>
						<Input
							type="password"
							value={password}
							onChange={handlePasswordChange}
						/>
					</FormItem>
					<FormItem>
						<Button type="submit" size="l" stretched>
							
						</Button>
					</FormItem>
					<FormItem>
						<Text>
							 ?{" "}
							<Link onClick={() => setActiveView("register")}></Link>
						</Text>
					</FormItem>
				</FormLayout>
			</Group>
		</>
	);
}

export default Login;
